@import url("https://fonts.googleapis.com/css2?family=MuseoModerno:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");
@import "vars";
// Responsive
@mixin tablets-only {
  @media screen and (min-width: map-get($breakpoints, tablets)) {
    @content;
  }
}

@mixin desktop-only {
  @media screen and (min-width: map-get($breakpoints, desktop)) {
    @content;
  }
}

// Fonts
@mixin principal-font {
  font-family: "MuseoModerno", cursive;
}

@mixin secondary-font {
  font-family: "Open-sans", sans-serif;
}
